
.v-code-container div {
  position: relative;
  font-size: 14px;
}
.v-code-container span.copy {
  border-radius: 0 0 3px 3px;
  padding: 2px 5px;
  position: absolute;
  top: 5px;
  right: 0;
  color: #b2b2b2;
  cursor: pointer;
}
.v-code-container span.copy:hover {
  color: #5c6b77;
}
.v-code-container .bg + span.copy {
  right: 5px;
}



